.dash-bg {
  position: relative;
  min-height: 100vh;
  background-size: cover;
  background-position: center;
  color: #2b2b2b;

  background-color: rgba(0, 0, 0, 0.6);
  background-blend-mode: overlay;
}
.dash-overlay {
  position: absolute;
  inset: 0;
  background: rgba(255, 255, 255, 0.78);
  z-index: 0;
}

.dash-topbar {
  position: relative;
  z-index: 1;
  background: #6f0d0d; 
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.85rem 1.25rem;
}
.user-chip {
  display: flex;
  align-items: center;
  gap: 0.7rem;
  font-weight: 600;
}
.user-chip .avatar {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background: #eee;
  background: url("../assets/mompfp.jpg") center/contain no-repeat;
}
.top-actions {
  display: flex;
  align-items: center;
  gap: 0.6rem;
}
.icon-btn {
  background: transparent;
  color: #fff;
  border: none;
  font-size: 1.2rem;
  cursor: pointer;
}
.icon-btn:hover {
  opacity: 0.85;
}

.dash-grid {
  position: relative;
  z-index: 1;
  max-width: 1200px;
  margin: 1.8rem auto;
  padding: 0 1rem;
  display: grid;
  grid-template-columns: 1fr 320px;
  gap: 1.5rem;
}

.dash-main {
  display: flex;
  flex-direction: column;
  gap: 1.4rem;
  position: relative;
}

.hero {
  background: transparent;
  padding: 0 0 1rem;
}
.hero-text h1 {
  color: #6f0d0d;
  font-size: 2rem;
  line-height: 1.25;
  margin: 0 0 0.6rem;
}
.hero-text p {
  color: #2b2b2b;
  margin: 0 0 1.2rem;
}

.about-btn-plain {
  background: #f4c542;
  color: #3b2b00;
  font-weight: 700;
  border: none;
  border-radius: 12px;
  padding: 0.65rem 1.4rem;
  cursor: pointer;
}
.about-btn-plain:hover {
  filter: brightness(0.96);
}

.section-header,
.cards {
  position: relative;
  z-index: 1;
}

.section-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.section-header h2 {
  color: #6f0d0d;
  margin: 0;
}
.see-all {
  background: none;
  border: none;
  color: #6f0d0d;
  font-weight: 600;
  cursor: pointer;
}

.cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.2rem;
}
.card {
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.7));
  border-radius: 14px;
  padding: 1.05rem 1.5rem;
  color: #2b2b2b;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.08);
  min-height: 300px;
  display: flex;
  flex-direction: column;
  gap: 0;
}
.card h3 {
  margin: 0 0 0.6rem;
  color: #6f0d0d;
  font-size: 1.25rem;
  font-weight: 700;
}
.card small {
  color: #6b6b6b;
  display: block;
  margin-bottom: 0.8rem;
  font-size: 0.9rem;
}
.card p {
  margin: 0;
  color: #333;
}
.read-btn {
  background: #ffd64d;
  color: #3b2b00;
  font-weight: 700;
  border: none;
  border-radius: 10px;
  padding: 0.6rem 1.1rem;
  font-size: 0.95rem;
  cursor: pointer;
  margin-top: auto;
  align-self: flex-start;
}
.read-btn:hover {
  background: #ffc933;
}

.parent-section-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #6f0d0d;
  color: #ffd64d;
  border-radius: 12px;
  padding: 0.6rem 0.9rem;
  margin-bottom: 0.9rem;
}

.dash-side {
  display: flex;
  flex-direction: column;
  gap: 1rem;

}
.sidebar-panel {
  background: #dedede;
  border-radius: 18px;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.sidebar-panel .profile-card {
  background: transparent; 
  box-shadow: none;
  padding: 0.5rem 0.5rem 0.8rem;
}
.profile-card {
  background: rgba(255, 255, 255, 0.9);
  border-radius: 16px;
  padding: 1.5rem;
  text-align: center;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.08);
}
.profile-avatar {
  width: 96px;
  height: 96px;
  border-radius: 50%;
  margin: 0.2rem auto 0.6rem;
  background: #e9e9e9;
  background: url("../assets/kid.jpg") center/contain no-repeat;
}
.profile-card h4 {
  margin: 0 0 0.8rem;
  color: #333;
}
.profile-actions {
  display: grid;
  gap: 0.7rem;
}
.pill {
  background: #7d2a2a;
  color: #fff;
  border: none;
  padding: 0.8rem 1rem;
  border-radius: 12px;
  font-weight: 600;
  cursor: pointer;
}
.pill:hover {
  filter: brightness(1.05);
}

.events-card {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 16px;
  padding: 1rem;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.08);
}
.events-card h4 {
  margin: 0 0 1rem;
  color: #6f0d0d; 
  font-weight: 700;
}
.event {
  display: flex;
  align-items: center;
  gap: 1rem;
  background: #e9e9e9; 
  border-radius: 16px; 
  padding: 0.9rem 1rem;
}
.event + .event {
  margin-top: 0.6rem;
}
.event-dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
}
.event-dot.red {
  background: #d64747;
}
.event-dot.green {
  background: #2ca86a;
}
.event-badge {
  width: 22px;          
  height: 36px;         
  background: #e00000;   
  border-radius: 12px;  
  flex: 0 0 22px;
  align-self: center;    
}
.event-body {
  display: flex;
  flex-direction: column;
}
.event-body strong {
  color: #2b2b2b;
  font-size: 0.95rem;
  font-weight: 700;
}
.event-body span {
  color: #666;
  font-size: 0.85rem;
}

.desc{
  font-size: 1rem;
  line-height: 1.6;
  color: #333;
}


@media (max-width: 1024px) {
  .dash-grid {
    grid-template-columns: 1fr;
  }
  .hero-illustration-standalone {
    position: static;
    transform: none;
    margin: 0 auto 1rem;
    width: min(650px, 95%); 
    height: 680px; 
  }
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 640px) {
  .hero-text h1 {
    font-size: 1.5rem;
  }
  .hero-illustration-standalone {
    height: 320px; 
    width: min(400px, 95%); 
  }
  .cards {
    grid-template-columns: 1fr;
  }
  .dash-topbar {
    flex-direction: column;
    gap: 0.6rem;
    align-items: flex-start;
  }
}

.cards .card {
  transition: transform 0.15s ease, box-shadow 0.15s ease, background 0.2s ease;
  cursor: pointer; 
}

.cards .card:hover {
  transform: translateY(-6px);
  box-shadow: 0 12px 28px rgba(0, 0, 0, 0.18);
  background: linear-gradient(180deg, rgba(255, 255, 255, 1), rgba(255, 255, 255, 0.85));
}


.cards .card:active {
  transform: scale(0.96);
  box-shadow: 0 4px 14px rgba(0, 0, 0, 0.2);
  animation: cardPop 0.2s ease;
}


@keyframes cardPop {
  0%   { transform: scale(1); }
  50%  { transform: scale(0.95); }
  100% { transform: scale(1); }
}


.cards .card .read-btn {
  transition: background 0.2s ease, transform 0.15s ease;
}
.cards .card .read-btn:hover {
  background: #ffc933;
  transform: translateY(-2px);
}
.cards .card .read-btn:active {
  transform: scale(0.95);
}



