.landing-background {
  min-height: 100vh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
}

.overlay {
  background: linear-gradient(
    rgba(40, 40, 40, 0.85),
    rgba(40, 40, 40, 0.85)
  );
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.landing-container {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  margin: 3rem auto;
  max-width: 1100px;
  width: 95%;

  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.2);

  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: white; /* make text readable */
}

.landing-left {
  flex: 1.2;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.landing-right {
  flex: 1;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: #570101; 
  color: white;
}

@media (max-width: 900px) {
  .landing-container {
    flex-direction: column;
    max-width: 90%;
  }

  .landing-right {
    border-radius: 0 0 12px 12px;
  }

  .landing-left {
    flex: 1;
  }
}

@media (max-width: 600px) {
  .landing-container {
    margin: 1rem;
  }

  .landing-left h1 {
    font-size: 2rem;
  }

  .landing-left h3 {
    font-size: 1.1rem;
  }

  .landing-left p {
    font-size: 0.95rem;
  }
}
