.teacher-attendance-container {
  display: flex;
  min-height: 100vh;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: hsl(0, 100%, 13%);
  overflow: hidden;
}

.teacher-sidebar {
  width: 250px;
  background-color: #4a0000;
  color: white;
  display: flex;
  flex-direction: column;
  padding: 1.5rem 1rem;
  border-right: 4px solid #4a0000;
  overflow: hidden;
}

.sidebar-header {
  border-bottom: 2px solid #b88a00;
  padding-bottom: 0.5rem;
  margin-bottom: 1rem;
}

.sidebar-header h1 {
  font-size: 0.9rem;
  font-weight: 700;
  letter-spacing: 1px;
}

.sidebar-header p {
  font-size: 0.7rem;
  color: #f0cfcf;
}

.teacher-info-wrapper {
  position: relative;
  margin-top: 1rem;
}

.teacher-info-wrapper::before {
  content: "";
  position: absolute;
  top: -0.6rem;
  left: 0;
  width: 100%;
  border-bottom: 2px solid #ab8922;
}

.teacher-info {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  padding: 0.8rem;
  background-color: #e0e0e0;
  border-radius: 12px;
  color: #3b0000;
}

.teacher-info .avatar {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  background-color: #bdbdbd;
}

.teacher-name {
  font-weight: 600;
  font-size: 0.9rem;
  margin: 0;
}

.teacher-role {
  font-size: 0.8rem;
  margin: 0;
  color: #5f5f5f;
}

.class-section {
  background: none;
  color: #fff;
  margin-top: 1rem;
  padding: 0.5rem 0.5rem 1rem;
  border-bottom: 1px solid #852424;
  position: relative;
}

.class-section h2 {
  font-size: 0.95rem;
  font-weight: 700;
  margin: 0;
}

.class-section .schedule {
  font-size: 0.75rem;
  color: #f1cfcf;
  line-height: 1.3;
  margin-top: 0.3rem;
}

.class-section .arrow {
  position: absolute;
  right: 0.5rem;
  top: 0.3rem;
  font-size: 1.1rem;
  color: #f8d14b;
  transform: rotate(180deg);
  cursor: pointer;
}

.sidebar-links {
  position: relative;
  margin-top: 3rem;
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}

.sidebar-links::before {
  content: "";
  position: absolute;
  top: -1rem;
  left: 0;
  width: 100%;
  border-bottom: 2px solid #ab8922;
}

.sidebar-links button {
  background-color: #f5f5f5;
  border: none;
  color: #410000;
  font-weight: 700;
  border-radius: 10px;
  padding: 0.9rem 1rem;
  text-align: left;
  font-size: 0.95rem;
  cursor: pointer;
  transition: all 0.2s ease;
}

.sidebar-links button:hover {
  background-color: #ffd54f;
  color: #630000;
  transform: scale(1.02);
}

.sidebar-links button.active {
  background-color: #ffd54f;
  color: #630000;
}

.teacher-main {
  flex: 1;
  background-color: white;
  margin: 1.5rem;
  border-radius: 1.5rem;
  padding: 2rem 3rem;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.25);
}

.teacher-main::before {
  content: "";
  position: absolute;
  inset: 0;
  background-image: url("../assets/shepbg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 1100px;
  opacity: 0.05;
  pointer-events: none;
  z-index: 0;
}

.teacher-main > * {
  position: relative;
  z-index: 1;
}

.attendance-container {
  max-width: 1200px;
  min-height: 200px;
  margin: 4rem auto 0;
  background-color: #ffffff;
  border-radius: 1rem;
  padding: 1.5rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
}

.header-box {
  background-color: #630000;
  color: #f1c93d;
  font-weight: 700;
  font-size: 1.1rem;
  border-radius: 0.7rem;
  height: 60px;
  line-height: 60px;
  padding: 0 1.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.15);
}

.header-box h2 {
  margin: 0;
  font-size: 1.25rem;
  font-weight: 800;
}

.header-box button {
  background-color: #ffd54f;
  color: #4a0000;
  font-weight: 700;
  border: none;
  border-radius: 6px;
  padding: 0.5rem 1.2rem;
  cursor: pointer;
  transition: all 0.2s ease;
}

.header-box button:hover {
  background-color: #f1c93d;
}

.filters {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 1.5rem;
}

.filters select {
  padding: 0.5rem 0.8rem;
  border-radius: 6px;
  border: 1px solid #bda76a;
  font-size: 0.9rem;
  background-color: #fffdf6;
}

.mark-all-btn {
  background-color: #ffd54f;
  color: #630000;
  font-weight: 700;
  border: none;
  border-radius: 6px;
  padding: 0.45rem 1rem;
  cursor: pointer;
  transition: all 0.2s ease;
}

.mark-all-btn:hover {
  background-color: #f1c93d;
}

/* Fixed behavior table styling */
.behavior-table-wrapper {
  overflow-x: auto;
  margin-top: 1rem;
}

.attendance-table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0;
  table-layout: auto;
}

.attendance-table thead {
  background-color: #f8f8f8;
  position: sticky;
  top: 0;
  z-index: 10;
}

.attendance-table th {
  text-align: left;
  color: #630000;
  font-weight: 700;
  font-size: 0.95rem;
  padding: 1rem 0.8rem;
  border-bottom: 2px solid #bda76a;
  white-space: nowrap;
}

.attendance-table th:nth-child(1) { width: 12%; } /* Date */
.attendance-table th:nth-child(2) { width: 15%; } /* Student */
.attendance-table th:nth-child(3) { width: 35%; } /* Incident */
.attendance-table th:nth-child(4) { width: 15%; } /* Action Taken */
.attendance-table th:nth-child(5) { width: 23%; } /* Actions */

.attendance-table tbody tr {
  background-color: #ffffff;
  border-bottom: 1px solid #e0e0e0;
  transition: background-color 0.2s ease;
}

.attendance-table tbody tr:hover {
  background-color: #fffdf6;
}

.attendance-table td {
  padding: 1rem 0.8rem;
  font-size: 0.9rem;
  vertical-align: middle;
  color: #333;
}

.attendance-table select,
.attendance-table input {
  width: 100%;
  padding: 0.5rem;
  border-radius: 5px;
  border: 1px solid #bda76a;
  font-size: 0.9rem;
  box-sizing: border-box;
}

/* Actions cell with properly aligned buttons */
.actions-cell {
  display: flex;
  gap: 8px;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.actions-cell button {
  padding: 8px 16px;
  border: none;
  border-radius: 6px;
  font-size: 0.85rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  white-space: nowrap;
}

.actions-cell .edit-btn {
  background-color: #ffd54f;
  color: #630000;
  min-width: 70px;
}

.actions-cell .edit-btn:hover {
  background-color: #f1c93d;
  transform: translateY(-1px);
}

.actions-cell .delete-btn {
  background-color: #8b0000;
  color: #fff;
  min-width: 70px;
}

.actions-cell .delete-btn:hover {
  background-color: #a00000;
  transform: translateY(-1px);
}

.actions-cell .view-btn {
  background-color: #4a0000;
  color: #ffd54f;
  min-width: 70px;
}

.actions-cell .view-btn:hover {
  background-color: #630000;
  transform: translateY(-1px);
}

/* Modal styling */
.behavior-modal-overlay {
  position: fixed;
  inset: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0,0,0,0.5);
  z-index: 9999;
}

.behavior-modal {
  background: #fff;
  border-radius: 12px;
  width: 500px;
  max-width: 90%;
  max-height: 75vh;
  overflow-y: auto;
  padding: 25px 30px;
  box-shadow: 0 8px 24px rgba(0,0,0,0.3);
  position: relative;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.modal-header h3 {
  color: #5d1227;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 0 0 1rem 0;
}

.modal-actions {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  margin-top: 15px;
}

.back-btn {
  background: #f8e4eb;
  color: #5d1227;
  border: none;
  padding: 8px 16px;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.2s ease;
}

.back-btn:hover {
  background: #f0d0dc;
}

.save-btn {
  background: #5d1227;
  color: #ffd54f;
  font-weight: 700;
  border: none;
  padding: 8px 16px;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.save-btn:hover {
  background: #7d1837;
}

.behavior-modal form label {
  display: block;
  font-weight: 600;
  color: #630000;
  margin-bottom: 0.3rem;
  font-size: 0.9rem;
}

.behavior-modal form input,
.behavior-modal form textarea,
.behavior-modal form select {
  width: 100%;
  box-sizing: border-box;
  padding: 8px;
  margin-bottom: 12px;
  border-radius: 6px;
  border: 1px solid #bda76a;
  font-size: 0.9rem;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.behavior-modal form textarea {
  min-height: 80px;
  resize: vertical;
}